
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Countup from 'vux/src/components/countup/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import ViewBox from '../../node_modules/vux/src/components/view-box/index.vue'
export default {

directives: {
TransferDom
},
components: {
ViewBox,
XHeader,
XButton,
Box,
XInput,
Group,
Cell,
Toast,
Tabbar,
TabbarItem,
Flexbox,
FlexboxItem,
Divider,
Countup,
Swiper,
SwiperItem,
Confirm,
XSwitch,
Datetime,
Blur,
Tab,
TabItem,
Scroller,
LoadMore,
Spinner,
XTable
},
computed: {
leftOptions () {
return {
showBack: true,
backText: ''
}
},
rightOptions () {
return {
showMore: false
}
}
},
filters: {
formatDate (value) {

return dateFormat(new Date(value), 'YYYY-MM-DD HH:mm:ss')
},
isSended (value) {
return value === true ? '已发货' : '待发货'
}
},
mounted () {
this.failureShowToast = false
this.changeSuccessShow = false
this.$vux.loading.show({
text: 'loading'
})
this.loadData()
},
data () {
return {
title: '已回答',
showConfirmBox: false,
computeDaysValue: '',
lists: [],
index: 0,
totalScore: 0,
computeHoursFunction (date, isToday, generateRange) {
if (isToday) {
return generateRange(new Date().getHours(), 23)
} else {
return generateRange(0, 23)
}
},
computeDaysFunction (options, generateRange) {
return [options.month] 
},
mobile: '',
password: '',
iconType: '',
successText: '',
changeSuccessShow: false,
changeFailureShow: false,
startCount: 0,
endCount: 100,
totalPages: 1002,
failureText: '',
noContentShow: false,
onFetching: false,
noContentText: '没有更多数据了！',
pullupEnabled: true,
pageSize: 5,
giftId: '',
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
}
}
},
methods: {
goDetail (giftOrderId) {
this.$router.push({name: 'ProductRedeemDetail', query: {giftOrderId: giftOrderId}})
},
loadMore () { // 暂时没有分页
},
loadData (params = {
userId: window.localStorage.getItem('userId'),
sortNum: -1
}) {
this.$http.post(this.$apis.userGetRedeemed, params).then(res => {
if (res.code === 200) {
console.log('res.data:' + res.data)
this.lists = res.data
this.$vux.loading.hide()
}
}).catch(function (error) {
console.log(error)
})
},
refresh () {
this.loadData()
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 10)
})
},
goRedeemDetail () {
this.$router.push('setting')
},
goStruction () {
this.$router.push('setting')
},
onIndexChange (newIndex, oldIndex) {
console.log(newIndex, oldIndex)
},
onCancel () {

},
onConfirm () {
this.$router.push('question')
},
onShow () {

},
onHide () {

},
change (val) {
console.log('on change', val)
},
onBlur (val) {
console.log('on blur', val)
},
onEnter (val) {
console.log('click enter!', val)
}
}
}



